/**
 * 
 */
export const success = (msg, callback) => {
	uni.showToast({
		title: msg,
		icon: 'success',
		mask: true,
		duration: 1500,
		success() {
			callback && callback()
		}
	})
}

/**
 * 
 */
export const error = (msg, callback) => {
	uni.showModal({
		title: '',
		content: msg,
		showCancel: false,
		success(res) {
			callback && callback()
		}
	})
}

/**
 * 
 */
export const toast = (msg, duration = 1500) => {
	uni.showToast({
		title: msg, // 
		icon: 'none',
		mask: true, // 
		duration // 1500	
	})
}